.print{
    margin: 90px auto;
    display: flex;
    padding: 80px 10px;
    justify-content: center;
    gap: 50px;
    background-color: rgb(219, 219, 219);
    border-radius: 50px;
    width: 800px;
    box-shadow: inset 2px 5px 10px rgba(0, 0, 0, 0.507);
}
.printEachImg img{
    border-radius: 10px;
    box-shadow: 2px 3px 8px rgba(0, 0, 0, 0.295);
}
.printEach{
    font-size: 20px;
    font-weight: 500;
    color: #444444;
}
.butsPrint{
    padding: 70px 0;
    display: flex;
    gap: 20px;
}
.shopNowBtn {
    padding: 15px 50px;
    border-radius: 10px;
    color: #ffff;
    font-weight: 500;
    font-size: 18px;
    border: none;
    background: linear-gradient(90deg,rgba(141, 44, 226, 1) 0%,rgba(76, 2, 224, 1) 100%);
    cursor: pointer;
    text-decoration: none;
}
.shopNowBtnWishlist{
    padding: 15px 50px;
    color: rgb(99, 33, 185);
    font-weight: 500;
    font-size: 18px;
    border-radius: 10px;
    border: 4px solid rgb(99, 33, 185);
    cursor: pointer;
    text-decoration: none; 
}